// iPhone X
$device-silver: #ebebe3;
$device-silver-dark: darken($device-silver, 10%);
$device-silver-panel: #222;
$height: 610px;
$width: 320px;

.device,
.device *,
.device ::after,
.device ::before,
.device::after,
.device::before {
  box-sizing: border-box;
}
.device {
  position: relative;
  transform: scale(1);
  z-index: 1;
}
.device .device-frame {
  z-index: 1;
}
.device .device-content {
  background-color: #000;
  background-position: center center;
  background-size: cover;
  object-fit: cover;
  position: relative;
}
.device-iphone-x {
  height: $height;
  width: $width;
  line-height: normal;

  .device-frame {
    background: $device-silver-panel;
    border-radius: 48px;
    box-shadow: inset 0 0 2px 2px $device-silver-dark,
      inset 0 0 0 7px $device-silver;
    height: $height;
    padding: 15px;
    width: $width;
  }
  .device-content {
    border-radius: 32px;
    height: $height - 30px;
    width: $width - 30px;
  }
  .device-top {
    width: 100%;
    height: 30px;
    display: block;
  }
  .device-top-left {
    float: left;
    display: inline;
    padding: 6px;
    padding-left: 30px;
  }

  .device-top-right {
    float: right;
    padding: 6px;
    padding-right: 14px;
  }

  .device-body {
    height: $height - 170px;
    margin-top: 24px;
  }
  .device-body-bottom-menu {
    height: 45px;
  }
  .menu-div {
    text-align: center;
    font-size: 10px;
  }

  .menu-div:nth-child(n + 5) {
    margin-top: 12px;
  }
  .menu-icon {
    height: 48px;
    width: 48px;
    opacity: 1;
  }
  .menu-icon:hover {
    opacity: 0.8;
  }

  .device-body-bottom {
    background-color: rgba(0, 0, 0, 0.4);
    border-radius: 25px;
    padding: 12px;
    margin: 0 10px;
  }
  .device-btns-home {
    position: absolute;
    bottom: 15px;
    left: 4.5vw;
    height: 5px;
    width: 150px;
    background-color: white;
    margin: 1px auto;
    border-radius: 50px;
    opacity: 1;
  }
  .device-btns-home:hover {
    opacity: 0.8;
  }

  .device-stripe {
    pointer-events: none;
    &::after,
    &::before {
      border: solid rgba(#333, 0.25);
      border-width: 0 7px;
      content: "";
      height: 7px;
      left: 0;
      position: absolute;
      width: 100%;
      z-index: 9;
    }
    &::after {
      top: 85px;
    }
    &::before {
      bottom: 85px;
    }
  }

  .device-header {
    background: $device-silver-panel;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    height: 30px;
    left: 50%;
    margin-left: -77px;
    position: absolute;
    top: 13px;
    width: 150px;

    &::after,
    &::before {
      content: "";
      height: 10px;
      position: absolute;
      top: 0;
      width: 10px;
    }
    &::after {
      background: radial-gradient(
        circle at bottom left,
        transparent 0,
        transparent 75%,
        $device-silver-panel 75%,
        $device-silver-panel 100%
      );
      left: -10px;
    }
    &::before {
      background: radial-gradient(
        circle at bottom right,
        transparent 0,
        transparent 75%,
        $device-silver-panel 75%,
        $device-silver-panel 100%
      );
      right: -10px;
    }
  }

  .device-sensors {
    &::after,
    &::before {
      content: "";
      position: absolute;
    }
    &::after {
      background: #444;
      border-radius: 3px;
      height: 6px;
      left: 50%;
      margin-left: -25px;
      top: 25px;
      width: 50px;
    }
    &::before {
      background: #444;
      border-radius: 50%;
      height: 14px;
      left: 50%;
      margin-left: 40px;
      top: 20px;
      width: 14px;
    }
  }
  .device-btns-mute {
    background: $device-silver-dark;
    height: 32px;
    left: -4px;
    position: absolute;
    top: 115px;
    width: 4px;
  }
  .device-btns-mute:hover {
    background-color: #333;
  }

  .device-btns {
    background: $device-silver-dark;
    height: 62px;
    left: -3px;
    position: absolute;
    top: 175px;
    width: 3px;

    &::after {
      background: $device-silver-dark;
      content: "";
      height: 62px;
      left: 0;
      position: absolute;
      top: 70px;
      width: 3px;
    }
  }

  .device-power {
    background: $device-silver-dark;
    height: 100px;
    right: -4px;
    position: absolute;
    top: 150px;
    width: 4px;
  }
  .device-power:hover {
    background-color: #333;
  }
}
