@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.bottomMenu {
  margin: auto;
  opacity: 1;
}
.bottomMenuActive {
  @extend .bottomMenu;
  color: #177ddc;
}
.bottomMenu:hover {
  opacity: 0.8;
}
.phoneNumber {
  height: 5vh;
  width: 100%;
  text-align: center;
}

.callButtons {
  height: 68px;
  max-width: 68px;
  padding: 5px;
  margin: 0 auto;
  margin-top: 10px;
  text-align: center;
  background-color: #343434;
  border-radius: 50%;
  opacity: 1;
}

.callButtons:hover {
  opacity: 0.8;
}

.phoneCallRemoveDigit {
  margin-left: 40px;
  margin-top: 20px;
  opacity: 1;
  animation: fadeIn linear 0.15s;
}
.phoneCallRemoveDigit:hover {
  opacity: 0.8;
}

.addContact {
  display: inline;
  color: #177ddc;
  font-size: 1em;
}
.addContact:hover {
  opacity: 0.8;
}
